        <i class="fa fa-angle-right"></i>
        <router-link :to="{ name: 'BRReception' }" class="breadcrumb__link"><i class='bx bx-window-open'></i></router-link>
      </div>
      <div class="breadcrumb__item">
        <i class="fa fa-angle-right"></i>
        <p>{{ $t('Сырье') }}</p>
      </div>
    </div>
    <div class="wrapper">
      <div v-if="infoAcceptItems.length != 0" class="card-custom">
