            <p class="w-3">{{ $t('state') }}</p>
            <p class="w-4"></p>
          </div>
          <div class="table-custom__body">
            <div v-for="item in infoAcceptItems" :key="item.id" class="table-custom__body-item">
              <p class="w-1">{{ item.sendBranchName }}</p>
              <p class="w-2">{{ item.sendDateTime | moment("DD/MM/YYYY - HH:mm") }}</p>
              <p class="w-3">{{ item.recvState == 1 ? $t('Ожидание приемки') : item.recvState == 2 ? $t('Принято') : $t('Отклонено') }}</p>
              <div class="w-4 btns">
                <button @click="openReceptionModal(item)" type="button" class="btn btn-success"><i class='bx bxs-plus-square'></i></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="modalInfo" class="modal-info">
      <div @click="closeReceptionModal()" class="close-btn">
        <i class='bx bxs-x-circle'></i>
      </div>
      <div class="modal-info__wrapper">
        <p>Время отправки: <span>{{ infoModal.sendDateTime | moment("DD/MM/YYYY - HH:mm") }}</span></p>
        <p>Откуда отправлено:	<span>{{ infoModal.sendBranchName }}</span></p>
        <p>Куда отправили:	<span>{{ infoModal.sentToBranchName }}</span></p>
        <p>Имя отправителя:	<span>{{ infoModal.sendUserName }}</span></p>
        <br>
        <p>Состояния:	<span>{{ infoModal.recvState == 1 ? $t('Ожидание приемки') : infoModal.recvState == 2 ? $t('Принято') : $t('Отклонено') }}</span></p>
        <p v-if="infoModal.recvState == 2">Время когда приняли:	<span>{{ infoModal.recvDateTime | moment("DD/MM/YYYY - HH:mm") }}</span></p>
        <p>Имя принимателя:	<span>{{ infoModal.recvUserName }}</span></p>
        <br>
        <div class="list">
          <h1>Список:</h1>
          <div v-for="item in infoModal.items" :key="item.index" class="list__item">
            <p>{{ item.itemTemplateName }}:</p>
            <p>{{ item.count }} {{ item.unit }}</p>
          </div>
        </div>
        <button v-if="infoModal.recvState != 2" @click="acceptItemClaim(infoModal.id)" type="button" class="btn btn-success w-100 mt-3">{{ $t('Принять') }}</button>
      </div>
    </div>
    <div v-show="modalInfo" class="modal-bg"></div>
    <Loading v-show="loading"/>
  </div>
</template>

<script>
import axios from 'axios';
import Loading from '@/components/Loading.vue';

export default {
  name: 'BRReceptionProduct',
  data() {
    return {
      loading: false,
      modalInfo: false,
      infoAcceptItems: [],
      infoModal: [],
      token: '',
      api: ''
    }
  },
  components: {
    Loading
  },
  beforeMount() {
    this.api = window.MY_CONFIG_BUYURSIN.api;
    // this.token = localStorage.token;
  },
  mounted() {    
    this.token = sessionStorage.getItem('token');
    this.loading = true;
    setInterval(() => {
      this.listAcceptItems();
    }, 3000);
  },
  methods: {
    listAcceptItems() {
      this.infoAcceptProducts = [];
      axios.get(`${this.api}/listAcceptItems/`, {
          headers: {
            'Authorization': `Token ${this.token}`
          }
        })
        .then(response => {
          // console.log(response.data);
          this.infoAcceptItems = response.data;
        })
        .catch(error => {
          // console.log(e.response);
          if(error.response.status == 401) {
            this.$toast.error(error.response.data.detail);
            sessionStorage.setItem('token', '');
            this.$router.push({ name: 'Auth' });
          }
        })
        .finally(()=> {
          this.loading = false;
        });
    },
    acceptItemClaim(id) {
      this.loading = true;
      axios.get(`${this.api}/acceptItemClaim/?claimId=${id}`, {
          headers: {
            'Authorization': `Token ${this.token}`
          }
        })
        .then(response => {
          if(response.data.code == 1) {
            this.$toast.success('Добавленно');
            this.listAcceptItems();
          } else {
            this.$toast.error(response.data.msg);
          }
        })
        
        .catch(error => {
          // console.log(e.response);
          if(error.response.status == 401) {
            this.$toast.error(error.response.data.detail);
            sessionStorage.setItem('token', '');
            this.$router.push({ name: 'Auth' });
          }
        })
        .finally(()=> {
          this.closeReceptionModal();
          this.loading = false;
        });
    },
    openReceptionModal(item) {
      // console.log(item);
      this.modalInfo = true;
      this.infoModal = item;
    },
    closeReceptionModal() {
      // console.log(item);
      this.modalInfo = false;
      this.infoModal = [];
    }
  },
}
</script>

