<template>
  <section class="BRFine">
    <div class="breadcrumb p-0">
      <router-link :to="{name: 'BRDashboard'}" class="breadcrumb__link"><i class="bx bx-home-alt"></i></router-link>
      <div class="breadcrumb__item">
        <i class="fa fa-angle-right"></i>
        <router-link :to="{name: 'BRPayList'}" class="breadcrumb__link"><i class="bx bx-money"></i></router-link>
      </div>
      <div class="breadcrumb__item">
        <i class="fa fa-angle-right"></i>
        <p>{{ $t('Штраф') }}</p>
      </div>
    </div>
    <div class="wrapper">
      <div class="card">
        <ul class="clps-menu">
          <li v-for="item in info" :key="item.id" class="clps-menu__item clps">
            <div @click="collapseToggle(item.id)" class="clps__button">
              <p class="d-flex align-items-center"><i class='ml-1 bx ' :class="[collapseActive == item.id ? 'bxs-down-arrow' : 'bxs-right-arrow']"></i> {{ item.name }}</p>
              <div class="btns">
                <!-- <button @click="changeModalAdd(1, item)" type="button" class="btn btn-warning"><i class='bx bx-edit'></i></button> -->
              </div>
            </div>
            <Transition duration="350" name="nested">
              <div v-if="collapseActive == item.id" class="clps__list list">
                <div class="custom-table">
                  <div v-if="item.list.length != 0" class="custom-table__header">
                    <p class="w-1">№</p>
                    <p class="w-2">{{ $t('Дата') }}</p>
                    <p class="w-3">{{ $t('Наз. штрафа') }}</p>
                    <p class="w-4">{{ $t('Штраф(сум)') }}</p>
                    <p class="w-5">{{ $t('') }}</p>
                    <p class="w-6"></p>
                  </div>
                  <div v-else class="custom-table__header">
                    <p>Пусто</p>
                  </div>
                  <div v-for="(itemList,index) in item.list" :key="itemList.index" class="custom-table__body">
                    <p class="w-1">{{ index + 1}}</p>
                    <p class="w-2">{{ itemList.date }}</p>
                    <p class="w-3">{{ itemList.name }}</p>
                    <p class="w-4">{{ itemList.price }}</p>
                    <p class="w-5"><span class="span-btn" :class="itemList.paid ? 'green' : 'red'">{{ itemList.paid ? '-' : '+' }}</span></p>
                    <div class="w-6 btns">
                      <button @click="changeModalAdd(itemList)" type="button" class="btn btn-warning"><i class='bx bx-edit'></i></button>
                      <button @click="openModalDel(itemList.id)" class="btn btn-danger"><i class="fa fa-trash-o" aria-hidden="true"></i></button>
                    </div>
                  </div>
                </div>
              </div>
            </Transition>
          </li>
        </ul>
      </div>
    </div>
    <div @click="openModalAdd()" class="btn-add">
      <i class="fa fa-plus-circle" aria-hidden="true"></i>
    </div>
    <div v-if="modalAdd" class="modal-add">
      <form class="form" action="">
        <div class="selects">
          <span>{{ $t('employes') }}</span>
          <multiselect v-model="employesValue" :class="$v.employesValue.$error ? 'is-invalid': ''" :disabled="btnChange ? true : false" placeholder="" label="name" track-by="id" :options="employesOptions" :multiple="false" :taggable="false" :searchable="false"></multiselect>
        </div>
        <div class="name">
          <span>{{ $t('name') }}</span>
          <input v-model="namePenalties" :class="$v.namePenalties.$error ? 'is-invalid': ''" class="input" type="text">
          <!-- <input v-model.number="countWork" :class="$v.countWork.$error ? 'is-invalid': ''" class="input" type="text"> -->
        </div>
        <div class="name">
          <span>{{ $t('price') }}</span>
          <input v-model="pricePenalties" :class="$v.pricePenalties.$error ? 'is-invalid': ''" class="input" type="text">
          <!-- <input v-model.number="countWork" :class="$v.countWork.$error ? 'is-invalid': ''" class="input" type="text"> -->
        </div>
        <div class="btns">
          <button v-if="btnChange != true" @click.prevent="addPenalties()" class="btn btn-success" type="button">{{ $t('add') }}</button>
          <button v-else-if="btnChange == true" @click.prevent="editPenalties()" class="btn btn-warning" type="button">{{ $t('change') }}</button>
          <button @click="closeModalAdd()" class="btn btn-outline-danger" type="button">{{ $t('cancel') }}</button>
        </div>
      </form>
    </div>
    <div v-if="modalDelete" class="modal-delete">
      <form @submit.prevent="delPenalties()" class="form" action="">
        <p>{{ $t('textDelete') }}</p>
        <div class="btns">
          <button class="btn btn-success" type="submit">{{ $t('yes')}}</button>
          <button @click="modalDelete = !modalDelete" class="btn btn-outline-danger" type="button">{{ $t('not')}}</button>
        </div>
      </form>
    </div>
    
    <div v-show="modalAdd || modalDelete" class="modal-bg"></div>
    <Loading v-show="loading"/>
  </section>
</template>

<script>
import axios from 'axios';
import Loading from '@/components/Loading.vue';
import { required } from 'vuelidate/lib/validators';

export default {
  name: 'BRFine',
  data() {
    return {
      loading: false,
      modalDelete: false,
      modalAdd: false,
      btnChange: false,
      collapseActive: 0,
      info: [],
      employesOptions: [],
      employesValue: '',
      namePenalties: '',
      paid: '',
      pricePenalties: '',
      penaltiesId: '',
      api: '',
      token: ''
    }
  },
  components: {
    Loading
  },
  beforeMount() {
    this.api = window.MY_CONFIG_BUYURSIN.api;
  },
  mounted() {
    this.token = sessionStorage.getItem('token');
    this.loading = true;
    this.employes();
    // this.worktemplates();
  },
  methods: {
    penalties() {
      axios.get(`${this.api}/penalties/`, {
          headers: {
            'Authorization': `Token ${this.token}`
          }
        })
        .then(response => {
          // console.log(response.data);
          for (let index = 0; index < this.info.length; index++) {
            for (let i = 0; i < response.data.length; i++) {
              if(this.info[index].id == response.data[i].employe) {
                // console.log('1 ' + this.info[index].id);
                // console.log('2 ' + response.data[i].employe);
                // console.log(response.data[i]);
                this.info[index].list.push(response.data[i]);
              }
            }
          }
          // this.info = response.data;
        })
        .catch(e => console.log(e))
        .finally(() => {
          this.loading = false;
        });
    },
    employes() {
      // this.productTemplatesColorOption = [];
      this.loading = true;
      this.info = [];
      axios.get(`${this.api}/employes/`, {
          headers: {
            'Authorization': `Token ${this.token}`
          }
        })
        .then(response => {
          this.employesOptions = response.data;
          for (let index = 0; index < response.data.length; index++) {
            this.info.push({
              "branch": response.data[index].branch,
              "department": response.data[index].department,
              "id": response.data[index].id,
              "jobTitle": response.data[index].jobTitle,
              "name": response.data[index].name,
              "list": []
            });
            let t = index + 1;
            if(response.data.length == t) {
              setTimeout(() => {
                this.penalties();
              }, 3000);
            }
          }
        })
        .catch(e => console.log(e))
        .finally(() => {
        });
    },
    // worktemplates() {
    //   // this.productTemplatesColorOption = [];
    //   axios.get(`${this.api}/worktemplates/`, {
    //       headers: {
    //         'Authorization': `Token ${this.token}`
    //       }
    //     })
    //     .then(response => {
    //       this.worktemplatesOptions = response.data;
    //     })
    //     .catch(e => console.log(e))
    //     .finally(() => {
    //       // this.loading = false;
    //     });
    // },
    addPenalties() {
      this.$v.$touch()
      if (this.$v.employesValue.$invalid && this.$v.namePenalties.$invalid && this.$v.pricePenalties.$invalid) {
        this.$toast.open({
          message: 'Ввидите данные правильно',
          type: "error"
        })
      } else {
        this.loading = true;
        axios.post(`${this.api}/penalties/`, {
            "employe": this.employesValue.id,
            "name": this.namePenalties,
            "price": this.pricePenalties
          }, 
          {
            headers: {
              'Authorization': `Token ${this.token}`
            }
          })
          .then(response => {
            if(response.data.code == 1) {
              this.$toast.success('Добавленно');
              // this.getBranchesGroups();
              this.employes();
              this.closeModalAdd();
            } else {
              this.$toast.error(response.data.msg);
            }
          })
          .catch(error => {
            console.log(error);
            if(error.response.status == 401) {
              this.$toast.error(error.response.data.detail);
            }
          })
          .finally(()=> {
          });
      }
    },
    delPenalties() {
      this.loading = true;
      axios.delete(`${this.api}/penalty-delete/${this.penaltiesId}/`, 
        {
          headers: {
            'Authorization': `Token ${this.token}`
          }
        })
        .then(response => {
          if(response.data.code == 1) {
            this.$toast.success('Удаленно');
            this.modalDelete = false;
            // this.getBranchesGroups();
            this.employes();
          } else {
            this.$toast.error(response.data.msg);
            this.loading = false;
          }
        })
        .catch(error => {
          // console.log(e.response);
          if(error.response.status == 401) {
            this.$toast.error(error.response.data.detail);
          }
        });
    },
    editPenalties() {
      this.$v.$touch()
      if (this.$v.employesValue.$invalid && this.$v.namePenalties.$invalid && this.$v.pricePenalties.$invalid) {
        this.$toast.open({
          message: 'Ввидите данные правильно',
          type: "error"
        })
      } else {
        this.loading = true;
        axios.put(`${this.api}/penalties/${this.penaltiesId}/`, {
            "employe": this.employesValue.id,
            "name": this.namePenalties,
            "price": this.pricePenalties
          }, 
          {
            headers: {
              'Authorization': `Token ${this.token}`
            }
          })
          .then(response => {
            if(response.data.code == 1) {
              this.$toast.success('Изменнено');
              // this.getBranchesGroups();
              this.employes();
            } else {
              this.$toast.error(response.data.msg);
            }
          })
          .catch(error => {
            console.log(error);
            if(error.response.status == 401) {
              this.$toast.error(error.response.data.detail);
            }
          })
          .finally(()=> {
            this.closeModalAdd();
            // this.loading = false;
          });
      }
    },
    openModalDel(id) {
      this.penaltiesId = id;
      this.modalDelete = true;
    },
    collapseToggle(id) {
      if(this.collapseActive == id) {
        this.collapseActive = 0
      } else {
        this.collapseActive = id;
      }
    },
    openModalAdd() {
      this.modalAdd = true;
    },
    changeModalAdd(info) {
      this.modalAdd = true;
      this.btnChange = true;
      // console.log(info);
      // this.worktemplatesValue = this.worktemplatesOptions.find(items => {
      //   if(items.id == info.work_template) {
      //     return items
      //   }
      // });
      this.pricePenalties = info.price;
      this.namePenalties = info.name;
      this.employesValue = this.employesOptions.find(items => {
        if(items.id == info.employe) {
          return items
        }
      });
      this.paid = info.paid;
      this.penaltiesId = info.id;
    },
    closeModalAdd() {
      this.modalAdd = false;
      this.modalDelete = false;
      this.btnChange = false;
      this.employesValue = [];
      this.worktemplatesValue = [];
      this.countWork = '';
      this.penaltiesId = '';
      this.$v.$reset();
    },
  },
  validations: {
    employesValue: {
      required
    },
    namePenalties: {
      required
    },
    pricePenalties: {
      required
    }
  }
}
</script>

<style lang="scss" scoped>
.BRFine {
  @media screen and (max-width: 991.5px) {
    margin-left: 0;
    padding: 0px 10px;
  }
  .breadcrumb {
    display: flex;
    align-items: center;
    background-color: transparent;
    margin: 0px 0 25px;
    &__item {
      display: flex;
      align-items: center;
      .fa {
        color: #002f34;
        font-size: 25px;
        margin: 0 10px;
      }
      p {
        font-family: 'Roboto';
        font-size: 18px;
        line-height: 18px;
        font-weight: 500;
        color: #002f34;
      }
    }
    &__link {
      display: flex;
      align-items: center;
      .bx {
        color: #002f34;
        font-size: 26px;
      }
    }
  }
  .card {
    background-color: #fff;
    background-clip: border-box;
    border: 0 solid transparent;
    border-radius: 0.25rem;
    box-shadow: 0 0.3rem 0.8rem rgba(0,0,0,0.12);
    padding: 1rem;
    overflow: scroll hidden;
    margin-bottom: 100px;
  }
  .clps-menu {
    min-width: 1000px;
    .clps {
      padding: 5px;
      &__button {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-family: "Roboto";
        font-size: 20px;
        font-weight: 400;
        background: rgba($color: #002f34, $alpha: 0.3);
        padding: 5px 20px;
        cursor: pointer;
        .btns {
          // width: 100px;
          display: flex;
          justify-content: space-between;
          .btn {
            display: flex;
            align-items: center;
            .bx {
              margin-right: 5px;
            }
          }
        }
        p {
          font-family: "Roboto";
          font-size: 20px;
          font-weight: 400;
          color: #002f34;
        }
      }
      .list {
        // height: 0;
        position: relative;
        &::before {
          content: "";
          width: 3px;
          height: 100%;
          background: rgba($color: #002f34, $alpha: 0.85);
          position: absolute;
          top: 0;
          left: 20px;
          @media screen and (max-width: 991.5px) {
            left: 10px;
          }
        }
        .custom-table {
          margin-left: 50px;
          margin-right: 100px;
          margin-bottom: 1px;
          @media screen and (max-width: 991.5px) {
            margin-left: 25px;
            margin-right: 0px;
          }
          &__header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: rgba(255, 255, 255, 0.12);
            padding: 5px 15px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.15);
            p {
              font-weight: 600 !important;
            }
          }
          &__body {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: rgba(41, 115, 65, 0.1);
            padding: 5px 15px;
            position: relative;
            margin-bottom: 2px;
            transition: 0.3s all ease-in-out;
            border-bottom: 1px solid rgba(255, 255, 255, 0.15);
            &::before {
              content: "";
              width: 28px;
              height: 3px;
              background: rgba($color: #002f34, $alpha: 0.85);
              position: absolute;
              top: 50%;
              left: -28px;
              transform: translateY(-50%);
              @media screen and (max-width: 991.5px) {
                width: 15px;
                top: 50%;
                left: -15px;
              }
            }
            &:hover {
              background: rgba($color: #002f34, $alpha: 0.04);
            }
            &-head {
              padding: 5px 15px;
              p {
                justify-content: flex-start !important;
              }
            }
            &-total {
              display: flex;
              justify-content: flex-end;
              align-items: flex-end;
              padding: 5px 15px;
              background: rgba($color: #002f34, $alpha: 0.2);
              // .w-6 {
              //   // justify-content: flex-end !important;
              //   // align-items: flex-end !important;
              //   // color: green;
              //   width: 20% !important;
              // }
            }
            .span-btn {
              font-family: "Roboto";
              font-size: 16px;
              font-weight: 500;
              color: #fff;
              padding: 0px 15px;
              border-radius: 5px;
            }
            .green {
              background: green;
            }
            .red {
              background: red;
            }
          }
          p {
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: "Roboto";
            font-size: 18px;
            font-weight: 400;
            color: rgba($color: #002f34, $alpha: 0.85);
          }
          .btns {
            display: flex;
            justify-content: space-between;
            .btn {
              // display: flex;
              padding: 0.2rem 0.5rem;
            }
          }
          .w-1 {
            width: 5%;
          }
          .w-2 {
            width: 15%;
          }
          .w-3 {
            width: 30%;
          }
          .w-4 {
            width: 27%;
          }
          .w-5 {
            width: 15%;
          }
          .w-6 {
            width: 80px;
          }
        }
      }
    }
  }
  .btn-add {
    width: 60px;
    height: 60px;
    position: fixed;
    bottom: 15px;
    right: 15px;
    padding: 10px;
    cursor: pointer;
    border-radius: 50%;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    .fa {
      font-size: 50px;
      color: #28a745;
    }
  }
  .modal-add {
    position: fixed;
    z-index: 11;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    border-radius: 0.5rem;
    padding: 12px;
    .text {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 2px solid #002f34;
      p {
        width: 50%;
        font-family: 'Roboto';
        font-size: 20px;
        font-weight: 400;
        padding: 5px 16px;
        color: rgb(129, 129, 129);
        text-align: center;
        transition: 0.3s all ease-in-out;
        cursor: pointer;
        @media screen and (max-width: 575.5px) {
          font-size: 18px;
        }
        &:hover {
          color: #000;
          background: rgb(230, 230, 230);
        }
        &:nth-child(2) {
          width: 50%;
        }
      }
      .active {
        color: #fff;
        background: #002f34;
        &:hover {
          color: #fff;
          background: #002f34;
        }
      }
    }
    .form {
      width: 400px;
      display: flex;
      flex-direction: column;
      margin-top: 15px;
      @media screen and (max-width: 575.5px) {
        width: 290px;
      }
      .input {
        width: 100%;
        margin-bottom: 12px;
      }
      .selects {
        position: relative;
        width: 100%;
        select {
          width: 100%;
          font-family: 'Roboto';
          font-size: 18px;
          font-weight: 400;
          color: #5A5A5A;
          border: 2px solid rgba(0,0,0,.12);
          border-radius: 5px;
          padding: 0.5em 0.7em;
          position: relative;
          background: transparent;
          z-index: 1;
          margin-bottom: 12px;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;
          cursor: pointer;
          &:hover {
            border-color: rgba(255, 255, 255, 0.2);
            transition: 0.3s all ease-in-out;
          }
          &:focus-visible {
            border-color: rgba(255, 255, 255, 0.2);
            outline: none;
          }
        }
        .fa {
          position: absolute;
          top: 7px;
          right: 14px;
          font-size: 30px;
          color: #5A5A5A;
        }
        select:hover + span + .fa,
        select:focus-visible + span + .fa {
          transition: 0.3s all ease-in-out;
          color: rgb(175, 175, 175);
        }
        select:hover + span,
        select:focus-visible + span {
          transition: 0.3s all ease-in-out;
          color: rgb(175, 175, 175) !important;
        }
        .multiselect {
          cursor: pointer;
          border: 2px solid rgba(0,0,0,.12);
          border-radius: 5px;
          margin-bottom: 12px;
          padding: 2px 0;
          font-family: 'Roboto';
          &:hover {
            border-color: rgb(175, 175, 175);
            transition: 0.3s all ease-in-out;
          }
        }
        .multiselect__single {
          font-family: 'Roboto';
        }
        span {
          padding: 2px 5px 0 5px !important;
          margin-bottom: 2px;
          z-index: 49;
        }
        .multiselect:hover+span {
          color: rgb(175, 175, 175);
          transition: 0.3s all ease-in-out;
        }
      }
      .btns {
        display: flex;
        justify-content: space-between;
        .btn {
          width: 48%;
          font-family: 'Roboto';
          padding: 8px 30px;
          text-transform: uppercase;
          @media screen and (max-width: 575.5px) {
            padding: 8px 16px;
          }
        }
      }
    }
  }
  .modal-delete {
    position: fixed;
    z-index: 11;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    border-radius: 0.5rem;
    padding: 12px;
    .form {
      width: 400px;
      display: flex;
      flex-direction: column;
      p {
        font-family: 'Roboto';
        font-size: 24px;
        line-height: 24px;
        font-weight: 400;
        color: #212529;
        margin-bottom: 15px;
        text-align: center;
      }
      .btns {
        display: flex;
        justify-content: space-between;
        .btn {
          width: 48%;
          font-family: 'Roboto';
          padding: 8px 30px;
          text-transform: uppercase;
        }
      }
    }
  }
  .name,
  .selects,
  .number,
  .name {
    position: relative;
    span {
      position: absolute;
      z-index: 2;
      top: 0%;
      left: 10px;
      transform: translateY(-50%);
      font-family: 'Roboto';
      font-size: 12px;
      font-weight: 400;
      color: rgba(0,0,0,.4);
      background: #fff;
      padding: 2px 5px;
    }
  }
  .input {
    width: 100%;
    font-family: 'Roboto';
    font-size: 18px;
    font-weight: 400;
    color: #5A5A5A;
    border: 2px solid rgba(0,0,0,.12);
    border-radius: 5px;
    padding: 0.5em 0.7em;
    position: relative;
    background: transparent;
    z-index: 1;
    margin: 0;
    &:hover {
      border-color: rgb(175, 175, 175);
      transition: 0.3s all ease-in-out;
    }
    &:focus-visible {
      border-color: rgb(175, 175, 175);
      outline: none;
    }
  }
  input:focus-visible+span{
    transition: 0.3s all ease-in-out;
