<template>
  <section class="dashboard">
    <div class="breadcrumb p-0">
      <router-link :to="{name: 'Dashboard'}" class="breadcrumb__link"><i class="bx bx-home-alt"></i></router-link>
      <div class="breadcrumb__item">
        <i class="fa fa-angle-right"></i>
        <p>{{ $t('dashboard') }}</p>
      </div>
    </div>
    <div class="wrapper">
      <div class="card">
        <ul class="clps-menu">
          <li v-for="item in info" :key="item.id" class="clps-menu__item clps">
            <div @click="collapseToggle(item.id)" class="clps__button">
              <p class="d-flex align-items-center">{{ item.branchName }} <i class='ml-1 bx ' :class="[collapseActive == item.id ? 'bxs-down-arrow' : 'bxs-right-arrow']"></i></p>
            </div>
            <Transition duration="350" name="nested">
              <div v-if="collapseActive == item.id" class="clps__list list">
                <div class="custom-table">
                  <div v-if="item.items.length != 0" class="custom-table__header">
                    <p class="w-1">№</p>
                    <p class="w-2">Код</p>
                    <p class="w-3">Название</p>
                    <p class="w-4"></p>
                  </div>
                  <div v-else class="custom-table__header">
                    <p>Пусто</p>
                  </div>
                  <div v-for="(itemList,index) in item.list" :key="itemList.Id" class="custom-table__body">
                    <p class="w-1">{{ index + 1}}</p>
                    <p class="w-2">{{ itemList.code }}</p>
                    <p class="w-3">{{ itemList.name }}</p>
                  </div>
                </div>
              </div>
            </Transition>
          </li>
        </ul>
      </div>
    </div>
    <Loading v-show="loading"/>
  </section>
</template>

<script>
import axios from "axios";
import Loading from '@/components/Loading.vue';

export default {
  name: 'Dashboard',
  data() {
    return {
      loading: false,
      info:[],
      collapseActive: '',
      token: '',
      api: ''
    }
  },
  components: {
    Loading
  },
  beforeMount() {
    this.api = window.MY_CONFIG_BUYURSIN.api;
  },
  mounted(){
    this.token = sessionStorage.getItem('token');
    this.loading = true;
    
    this.listSoldProducts();
  },
  methods: {
    // openMenu() {
    //   this.$refs.adMenu.openMenu();
    // },
    // onLoad() {
    //   let auth = JSON.parse(window.sessionStorage.getItem("auth"));
    //   if(auth === false || auth === null || auth === undefined) {
    //     // this.$router.push({ name: 'Admin' });
    //     window.history.go(-1);
    //   }
    // },
    // onUnload() {
    //   window.sessionStorage.setItem("auth", false);
    // },
    listSoldProducts() {
      axios.get(`${this.api}/listSoldProducts/`, {
          headers: {
            'Authorization': `Token ${this.token}`
          }
        })
        .then(response => {
          // console.log(response.data);
          // this.info = response.data;
          for(let i = 0; i < response.data.length; i++) {
            const list = {
              'id': i + 1,
              'branchName': response.data[i].branchName,
              'items': response.data[i].items
            }
            this.info.push(list);
          }
          // for (let index = 0; index < response.data.length; index++) {
          //   const element = {
          //     code: response.data[index].id,
          //     name: response.data[index].name
          //   }
          //   this.metterGroupNameOption.push(element)
          // }
          // this.getMetter();
        })
        .catch(error => {
          // console.log(e.response);
          if(error.response.status == 401) {
            this.$toast.error(error.response.data.detail);
            sessionStorage.setItem('token', '');
            this.$router.push({ name: 'Auth' });
          }
        })
        .finally(() => { this.loading = false; });
    },
    collapseToggle(id) {
      if(this.collapseActive == id) {
        this.collapseActive = 0
      } else {
        this.collapseActive = id;
      }
    },
  }
}
</script>

