<template>
  <section class="SCHistory">
    <div class="breadcrumb p-0">
      <router-link :to="{name: 'SCDashborad'}" class="breadcrumb__link"><i class="bx bx-home-alt"></i></router-link>
      <div class="breadcrumb__item">
        <i class="fa fa-angle-right"></i>
        <p>{{ $t('history') }}</p>
      </div>
    </div>
    <div class="wrapper">
      <div class="card-custom">
        <div class="table-custom">
          <div class="table-custom__header">
            <p class="w-1">{{ $t('date') }}</p>
            <p class="w-2">{{ $t('name') }}</p>
            <p class="w-3">{{ $t('name') }}</p>
            <p class="w-4">{{ $t('count') }}</p>
          </div>
          <div class="table-custom__body">
            <div v-for="item in info" :key="item.id" class="table-custom__body-item">
              <p class="w-1">{{ item.datetime | moment("DD/MM/YYYY - HH:mm") }}</p>
              <p class="w-2">{{ item.userName }}</p>
              <p class="w-3">{{ item.productName }}</p>
              <p class="w-4">{{ item.count }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Loading v-show="loading"/>
  </section>
</template>

<script>
import axios from 'axios';
import Loading from '@/components/Loading.vue';
// import moment from 'moment';

export default {
  name: 'SCHistory',
  data() {
    return {
      loading: false,
      modalDelete: false,
      btnChange: false,
      modalAdd: false,
      modalInfo: false,
      infoModal: [],
      colorId: '',
      colorCode: '',
      colorName: '',
      info: [],
      token: '',
      api: '',
    }
  },
  components: {
    Loading
  },
  beforeMount() {
    this.api = window.MY_CONFIG_BUYURSIN.api;
    // this.token = localStorage.token;
  },
  mounted() {
    this.token = sessionStorage.getItem('token');
    this.listSoldProducts();
  },
  methods: {
    listSoldProducts() {
      this.loading = true;
      this.info = [];
      this.infoSend = [];
      axios.get(`${this.api}/listSoldProducts/`, {
          headers: {
            'Authorization': `Token ${this.token}`
          }
        })
        .then(response => {
          this.info = response.data;
        })
        .catch(error => {
          // console.log(e.response);
          if(error.response.status == 401) {
            this.$toast.error(error.response.data.detail);
            sessionStorage.setItem('token', '');
            this.$router.push({ name: 'Auth' });
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
  }
}
</script>

<style lang="scss" scoped>
.SCHistory {
  @media screen and (max-width: 991.5px) {
    margin-left: 0;
    padding: 0px 10px;
